import {
  AfterViewChecked,
  ChangeDetectorRef,
  Component,
  HostListener,
  OnDestroy,
  OnInit,
} from '@angular/core';
import { Router, RouterOutlet, RouterStateSnapshot } from '@angular/router';
import { Observable, of, Subscription, from } from 'rxjs';
import { openCloseAnimation } from 'src/app/fade-in-transition';
import { SharedService } from 'src/app/shared/shared.service';
import { Title } from '@angular/platform-browser';
import { ConfirmDialogService } from '../../../confirmDialog.service';
import { ShopvueTitleService } from 'src/app/shopvue-title.service';
import { ComponentCanDeactivate } from 'src/app/pending-changes.guard';

@Component({
  selector: 'app-review-employee-day',
  templateUrl: './review-employee-day.component.html',
  styleUrls: ['./review-employee-day.component.scss'],
  animations: [openCloseAnimation],
})
export class ReviewEmployeeDayComponent
  implements OnInit, OnDestroy, AfterViewChecked, ComponentCanDeactivate {
  @HostListener('window:beforeunload', ['$event'])
  canRefresh(event: Event): boolean | Observable<boolean> {
    if (this.value !== '' || this.saving) {
      event.preventDefault();
      event.returnValue = false;
      return false;
    } else {
      return true;
    }
  }

  canDeactivate(): boolean | Observable<boolean> {
    if (this.value !== '' && !this.saving) {
      return this.confirmDialogService.confirm();
    } else if (this.saving) {
      return this.confirmDialogService.confirm(
        'Saving in progress, please wait before exiting',
        'Saving in progress',
        false
      );
    } else {
      return true;
    }
  }

  private subscription?: Subscription;
  saving: boolean = false;
  watchDogActive: boolean = false;
  value = '';

  pageButtons = [
    {
      name: 'Close',
      link: '..',
    },
    {
      name: 'Edit Attendance',
      link: 'edit-attendance',
    },
  ];

  constructor(
    private sharedService: SharedService,
    public confirmDialogService: ConfirmDialogService,
    private changeDetectorRef: ChangeDetectorRef,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.subscription = this.sharedService.changeEmitted$.subscribe(
      (text) => (this.value = text)
    );
  }

  ngAfterViewChecked(): void {}

  ngOnDestroy(): void {
    this.subscription?.unsubscribe();
  }

  prepareRoute(outlet: RouterOutlet) {
    return outlet && outlet.activatedRouteData;
  }

  startSaveProcess() {
    this.saving = true;

    setTimeout(() => {
      this.completedSaveProcess();
    }, 20000);
  }

  completedSaveProcess() {
    this.saving = false;
  }

  activateWatchDog() {
    this.watchDogActive = !this.watchDogActive;
  }

  saveChanges() {
    this.changeDetectorRef.detectChanges();
  }
}
