import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { combineAll } from 'rxjs/operators';
import { PendingChangesGuard } from '../pending-changes.guard';
import { ChangeSchedulePatternComponent } from './components/change-schedule-pattern/change-schedule-pattern.component';
import { EditAttendanceComponent } from './components/edit-attendance/edit-attendance.component';
import { ReviewEmployeeDayComponent } from './components/review-employee-day/review-employee-day.component';
import { WeekAtAGlance } from './pages/week-at-a-glance/week-at-a-glance.component';
import { AreaSelectionComponent } from './components/area-selection/area-selection.component';

const routes: Routes = [
  {
    path: '',
    component: WeekAtAGlance,
    data: { pageTitle: 'Week at a Glance' },
    children: [
      {
        path: 'change-schedule-pattern',
        component: ChangeSchedulePatternComponent,
        data: { pageTitle: 'Change Schedule Pattern' },
      },
      {
        path: 'review-employee-day',
        component: ReviewEmployeeDayComponent,
        canDeactivate: [PendingChangesGuard],
        data: { pageTitle: 'Review Employee Day' },
        children: [
          {
            path: 'edit-attendance',
            component: EditAttendanceComponent,
            canDeactivate: [PendingChangesGuard],
            data: { pageTitle: 'Edit Attendance' },
          },
        ],
      },
      {
        path: 'area-selection',
        component: AreaSelectionComponent,
        data: { pageTitle: 'Area Selection' },
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class WeekAtAGlanceRoutingModule {}
