import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { DxTreeViewComponent } from 'devextreme-angular';
import {
  selectionData,
  area,
  machineData,
  workpointData,
  departmentData,
  departmentWorkcenterData,
  workcenterData,
} from './area-selection.data';
import { DxRadioGroupModule } from 'devextreme-angular';

@Component({
  selector: 'app-area-selection',
  templateUrl: './area-selection.component.html',
  styleUrls: ['./area-selection.component.scss'],
})
export class AreaSelectionComponent implements OnInit {
  constructor(private changeDetector: ChangeDetectorRef) {}

  selectionData?: Array<area>;
  treeBoxValue?: area;
  selectedRows: Array<number> = [];
  isTreeOpened: boolean = false;
  gridBoxValue: area[] = Array<area>();
  isGridOpened: boolean = false;
  areaSearchValue: string = '';
  toolTipVisible: boolean = false;
  tooltipContent: string = '';

  dataSource: Array<area> = [];
  currentDataSource: string = 'selectionData';
  modes: string[] = [
    'Any',
    'Department',
    'Department and Workcenter',
    'Workcenter',
    'Workpoints',
  ];
  @ViewChild('treeView')
  treeView?: DxTreeViewComponent;

  selectionModes: string[] = ['Single', 'Multiple'];
  currentSelectionMode = 'single';
  showCheckBoxes = 'none';
  ngOnInit(): void {
    this.dataSource = selectionData;
  }

  syncTreeViewSelection(e: any) {
    if (!this.treeView) return;

    if (!this.treeBoxValue) {
      this.treeView.instance.unselectAll();
    } else {
      this.treeView.instance.selectItem(this.treeBoxValue);
    }
  }

  treeView_itemSelectionChanged(e: any) {
    this.selectedRows = e.component.getSelectedNodeKeys();
    const selectedNodes = e.component.getSelectedNodes();
    if (selectedNodes.length > 0) {
      this.treeBoxValue = selectedNodes[0].itemData;

      this.generateToolTipContent();
    } else {
      this.tooltipContent = '';
    }
  }

  onTreeBoxOptionChanged(e: any) {
    if (e.name === 'value' && this.currentSelectionMode === 'single') {
      this.isTreeOpened = false;
    }
  }

  checkIfNodeHasChildren = (event: any): void => {
    console.log('Check if node has children');
    if (
      this.currentDataSource === 'machineData' ||
      this.currentDataSource === 'workpointData'
    ) {
      if (
        !event.itemData.machineID &&
        !event.itemData.workpointID &&
        this.currentSelectionMode === 'single'
      ) {
        event.event.stopPropagation();
        event.event.preventDefault();
      }
    } else if (this.currentDataSource === 'workcenterData') {
      if (
        !event.itemData.machineID &&
        !event.itemData.workpointID &&
        !event.itemData.workCenterID &&
        this.currentSelectionMode === 'single'
      ) {
        event.event.stopPropagation();
        event.event.preventDefault();
      }
    }
  };

  syncGridViewSelection(e: any) {
    if (!this.treeView) return;

    if (!this.gridBoxValue) {
      this.treeView.instance.unselectAll();
    } else {
      this.treeView.instance.selectItem(this.gridBoxValue);
    }
  }

  gridView_itemSelectionChanged(e: any) {
    this.gridBoxValue = e.component.getSelectedNodeKeys();
  }

  onGridBoxOptionChanged(e: any) {
    if (e.name === 'value') {
      this.isGridOpened = false;
    }
  }

  displayStringCustomization(item: area): string {
    // "item" can be null
    if (item.machineID !== null) {
      return item.machineID + ' - ' + item.description;
    } else if (item.workpointID !== null) {
      return item.workpointID + ' - ' + item.description;
    } else if (item.workCenterID !== null) {
      return item.workCenterID + ' - ' + item.description;
    } else {
      return item.departmentID + ' - ' + item.description;
    }
  }

  generateToolTipContent = () => {
    if (this.treeBoxValue) {
      if (this.treeBoxValue.workCenterID !== null) {
        this.tooltipContent =
          this.treeBoxValue.workCenterID +
          ' - ' +
          this.treeBoxValue.description;
      } else {
        this.tooltipContent =
          this.treeBoxValue.departmentID +
          ' - ' +
          this.treeBoxValue.description;
      }
    }
  };

  displayGridStringCustomization = (event: any) => {
    // "item" can be null
    if (event.workCenterID !== null) {
      return event.workCenterID + ' - ' + event.description;
    } else {
      return event.departmentID + ' - ' + event.description;
    }
  };

  toggleTooltip(show: boolean): void {
    if (this.treeBoxValue) {
      this.toolTipVisible = show;
    }
  }

  changeSelectionOption(event: any) {
    this.selectedRows = [];
    if (event.value === 'Any') {
      this.currentDataSource = 'selectionData';
      this.dataSource = selectionData;
    } else if (event.value === 'Department') {
      this.currentDataSource = 'departmentData';
      this.dataSource = departmentData;
    } else if (event.value === 'Department and Workcenter') {
      this.currentDataSource = 'departmentWorkcenterData';
      this.dataSource = departmentWorkcenterData;
    } else if (event.value === 'Workcenter') {
      this.currentDataSource = 'workcenterData';
      this.dataSource = workcenterData;
    } else if (event.value === 'Workpoints') {
      this.currentDataSource = 'workpointData';
      this.dataSource = workpointData;
    }

    this.changeDetector.detectChanges();
  }

  changeSelectionMode(event: any) {
    this.selectedRows = [];
    if (event.value === 'Single') {
      this.currentSelectionMode = 'single';
      this.showCheckBoxes = 'none';
    } else if (event.value === 'Multiple') {
      this.currentSelectionMode = 'multiple';
      this.showCheckBoxes = 'normal';
    }
  }
}
