import {
  AfterViewChecked,
  Component,
  HostListener,
  OnInit,
} from '@angular/core';
import { Observable } from 'rxjs';
import { ConfirmDialogService } from 'src/app/confirmDialog.service';
import { SharedService } from 'src/app/shared/shared.service';

@Component({
  selector: 'app-edit-attendance',
  templateUrl: './edit-attendance.component.html',
  styleUrls: ['./edit-attendance.component.scss'],
})
export class EditAttendanceComponent implements OnInit, AfterViewChecked {
  @HostListener('window:beforeunload')
  canDeactivate(): Observable<boolean> | boolean {
    if (this.value !== '') {
      return this.confirmDialogService.confirm();
    } else {
      return true;
    }
  }

  currentDate?: Date;
  value = '';

  constructor(
    private sharedService: SharedService,
    public confirmDialogService: ConfirmDialogService
  ) {}

  ngOnInit(): void {}

  ngAfterViewChecked(): void {}

  onChange = (e: any) => this.sharedService.emitChange(e.event.target.value);
}
